.testcards {
  display: flex;
  flex-direction: column;
  .secondcard {
    position: relative;
    margin-top: 50px;
  }
  .cardwords {
    color: white;
    position: absolute;
    top: 20%;
    font-size: 20px;
    text-align: center;
    margin-bottom: 0px;
  }
  .btn {
    font-size: 20px;
  }
}
.testbotton {
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 0px;
}
@media screen and (max-width: 993px) {
  .testcards {
    .secondcard {
      margin-top: 0px;
    }
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
}
@media screen and (max-width: 446px) {
  .testcards {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
}
@media screen and (max-width: 1208px) {
  .testcards {
    .cardwords {
      color: white;
      position: absolute;
      top: 10%;
      font-size: 16px;
      text-align: center;
      margin-bottom: 0px;
    }
    .btn {
      font-size: 16px;
    }
  }
}
