.TestPage {
  h2 {
    text-align: center;
    font-size: 24px !important;
  }
  p {
    text-align: center;
  }
  a {
    margin: 20px !important;
  }

  span {
    font-size: 20px;
    font-weight: bold;
    //color: rgb(255 193 8);
  }
  @media screen and (min-width: 500px) {
    .two-col-section {
      width: 80vw;
      margin: 20px auto;
      margin-bottom: 200px;
      display: flex;
      justify-content: space-evenly;
      .two-col-section-col {
        width: 30vw;
        justify-content: flex-start;
        p {
          text-align: justify !important;
          font-weight: bold;
        }
      }
    }
  }

  @media screen and (max-width: 500px) {
    .two-col-section {
      margin: 20px auto;
      display: inline;
      .two-col-section-col {
        width: 80vw;
        p {
          text-align: justify !important;
          font-weight: bold;
        }
      }
    }
  }
}
