.position-sticky{
  width: 100vw;
  margin-top: 50px;
  clear: both;
  background-color: rgb(243, 243, 243);
  display: flex;
  justify-content: space-evenly;
  p{
    color: #999;
    text-align: center;
    margin: 42px 0px;
  }
  svg{
    margin: 42px 0px;
  }
  .three-line{
    margin: 18px 0px;
    float: right;
    p{
      display: flex;
      flex-direction: column;
      margin: 0px;
      text-align:left;
    }
    
  }
}