* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial;
}

main {
  width: 80vw;
  margin: 100px auto;
}

input {
  outline: none;
}

h1 {
  font-weight: bold;
  margin-top: 20px;
  color: rgb(45 98 124);
}

h2 {
  font-weight: bold;
  color: rgb(45 98 124);
}

h3 {
  font-weight: bold;
  color: rgb(45 98 124);
}

h4 {
  font-weight: bold;
  color: rgb(45 98 124);
}

h5 {
  font-weight: bold;
  color: rgb(45 98 124);
}

a {
  text-decoration: none;
  color: #222;
}

li {
  list-style: none;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.two-col-section {
  width: 80vw;
  margin: 20px auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;

  .two-col-section-col {
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    svg {
      margin: 0 auto;
    }

    h2 {
      margin: 1vw 0;
      font-size: 20px;
      text-align: center;
    }

    a {
      text-align: center;
      margin-top: auto;
    }

    p {
      text-align: center;
    }
  }
}
.btn-info {
  background-color: rgb(45 98 124);
  border-color: rgb(45 98 124);
  font-size: 20px;
  padding: 3px 10px;
}
.btn-info:hover {
  background-color: rgb(19 114 131);
  font-size: 20px;
  padding: 3px 10px;
}
.link-home {
  width: 100%;
  margin: 10px;
  height: 1px;
  border-top: solid rgb(45 98 124) 2px;
}

.link-top {
  width: 80%;
  margin: 10px 10%;
  height: 1px;
  border-top: solid #acc0d8 1px;
}
.driving-link-top {
  width: 40px;
  margin: 10px 0px;
  border-top: solid rgb(57, 118, 143) 3px;
}

.link-bottom {
  //width: 50%;
  margin: 10px;
  height: 1px;

  border-bottom: solid #acc0d8 1px;
}

.link-right {
  margin: 10px;
  height: 20%;
  border-right: solid black 1px;
}

p.ref {
  font-size: 16px;
  color: rgb(109, 108, 108);
}
@media screen and (min-width: 500px) {
  .os_widget {
    width: 60vw;
    height: 80vh;
    margin: 100px auto;
  }
}
@media screen and (max-width: 500px) {
  .os_widget {
    width: 80vw;
    margin: 30px auto;
  }
}
.intro {
  display: inline;
  text-align: left;
  color: rgb(30 89 129);
  font-weight: bold;
  font-size: 20px;
  span {
    color: rgb(30 89 129);
    font-size: 16px;
    font-weight: bold;
  }
}
//in a row.
.nav-link {
  padding: 0;
}
@media print {
  a {
    display: none;
  }
  .intro {
    display: none;
  }
  .three-line {
    display: none;
  }
}
