.navigationbar {
  // box-shadow: 0px 2px 1px #d4d4d4;
  background-color: RGB(229, 235, 238);
  p {
    padding-left: 5px;
    padding-right: 5px;
  }
  @media screen and (max-width: 480px) {
    img {
      height: 40px;
      width: 200px;
    }
  }
  .notactive {
    font-size: 16px;
    font-weight: bold;
    margin: 0px;
    color: rgb(45 98 124);
    #collasible-nav-dropdown {
      font-size: 16px;
      font-weight: bold;
      margin: 0px;
      color: rgb(27 146 169);
    }
  }
  .active {
    font-size: 16px;
    font-weight: bold;
    margin: 0px;
    color: black;
  }
  #drop-items {
    background-color: RGB(234, 239, 241);
  }
  .dropdown-menu {
    background-color: RGB(234, 239, 241);
  }
}
